.Logo {
  min-width: 38px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background-image: url(../../images/logo/logo.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.Logo:hover {
  opacity: .8;
  cursor: pointer;
  box-shadow: 0 6px 5px rgba(0, 0, 0, .1), 0 6px 10px;
}